:root {
  --moderate-cyan: hsl(176, 50%, 47%);
  --dark-cyan: hsl(176, 72%, 28%);
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
}

:root {
  --font-commissioner: "Commissioner", sans-serif;
}

body {
  font-family: var(--font-commissioner);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-commissioner);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a::visited,
a:active {
  text-decoration: none;
}

